import moment from "moment";
import * as actionTypes from "../actions/ActionTypes";

const initialState = {
  walletList: [],
};


const setWalletList = (state, {walletList, ...rest}) => {
  return {
    ...state,
    ...{
      walletList: walletList,
    },
  };
};

const setCurrentWallet = (state, {currentWallet, ...rest}) => {
  return {
    ...state,
    ...{
      currentWallet: currentWallet,
    },
  };
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_WALLET_LIST:
      return setWalletList(state, action);
    case actionTypes.SET_CURRENT_WALLET:
      return setCurrentWallet(state, action);
    default:
      return state;
  }
};

export default reducer;
