import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import useStyles from './style';
import {
  Metamask,
  WalletConnect,
  Trust,
  Coinbase,
  EncryptedInk,
  Fortmatic
} from './walletImgs';
import { Grid } from '@material-ui/core';
import { Wallet, ConnectModal } from './components';
import { LedgerIcon, MagicIcon } from './icons/icons';
import { WALLETS } from 'utils/constants';
import { UnsupportedChainIdError, useWeb3React } from '@web3-react/core';
import { setCurrentWallet, setWalletList } from 'store/actions/App';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';

const ConnectWallet = props => {
  const classes = useStyles();
  const { activate, deactivate, account, library, chainId } = useWeb3React();

  const [modalOpen, setModalOpen] = useState(false);
  const [walletInfo, setWalletInfo] = useState(null);
  const dispatch = useDispatch()
  const history = useHistory()
  useEffect(() => {
    console.log(account)
    if (account) {
      let walletList = JSON.parse(localStorage.getItem('wallets') || '[]');
      console.log(walletList, account, !walletList.find(checkWallet));
      if (!walletList.find(checkWallet)) {
        walletList.push({ address: account.toLowerCase() });
        localStorage.setItem('wallets', JSON.stringify(walletList));
      }
      dispatch(setWalletList(walletList));
      dispatch(setCurrentWallet(account))
    }
  }, [account]);

  const checkWallet = wallet => {
    return wallet.address === account.toLowerCase();
  };

  const handleModalClose = () => {
    setModalOpen(false);
  };
  const handleConnectWallet = walletInfo => {
    const { connector, type } = walletInfo;
    // setSelectedWalletInfo(walletInfo)
    setWalletInfo(walletInfo);
    if (walletInfo.type !== 'fortmatic')
      setModalOpen(true);
    if (connector) {
      activate(connector, undefined, true)
        .then(async res => {
          //   await switchNetwork('0x4')
          // setStep(1)
          console.log(res);
          setTimeout(() => {
            setModalOpen(false);
          }, 2000);
        })
        .catch(error => {
          if (error instanceof UnsupportedChainIdError) {
            activate(connector);
          } else {
            setModalOpen(false);
            if (error.code == 4001) {
            } else if (type === 'metamask') {
            }
            console.info('Connection Error - ', error);
          }
        })
        .finally(() => {});
    }
  };

  return (
    <div className={classes.root}>
      <div className={classes.container}>
        <Grid container spacing={4}>
          <Wallet
            src={Metamask}
            title="Metamask"
            onClick={() => handleConnectWallet(WALLETS[0])}
          />
          <Wallet
            src={WalletConnect}
            title="WalletConnect"
            onClick={() => handleConnectWallet(WALLETS[1])}
          />
          <Wallet
            src={Trust}
            title="Trust"
            onClick={() => handleConnectWallet(WALLETS[1])}
          />
          <Wallet
            src={Coinbase}
            title="Coinbase Wallet"
            onClick={() => handleConnectWallet(WALLETS[2])}
          />
          <Wallet
            src={EncryptedInk}
            title="Encrypted Ink"
            onClick={() => handleConnectWallet(WALLETS[1])}
          />
          <Wallet
            src={<LedgerIcon />}
            title="Ledger"
            type="svg"
            onClick={() => handleConnectWallet(WALLETS[3])}
          />
          <Wallet
            src={Fortmatic}
            title="Fortmatic"
            onClick={() => {
              handleConnectWallet(WALLETS[4])
            }}
          />
          <Wallet src={<MagicIcon />} title="Connect with email" type="svg" onClick={() => history.push('/connect-wallet/magic')}/>
        </Grid>
      </div>
      <ConnectModal
        open={modalOpen}
        handleClose={handleModalClose}
        account={account}
        walletInfo={walletInfo}
      />
    </div>
  );
};

export default ConnectWallet;
