import { Grid } from '@material-ui/core';
import React, { useState } from 'react';
import useStyles from './style';

const Wallet = props => {
  const {title, src, type, onClick} = props;
  const classes = useStyles();

  return (
    <Grid item xs={6} sm={3} className={classes.container} onClick={onClick}>
        {
                type === "svg" 
            ? 
                src
            :
                <img src={src} className={classes.icon}></img>
        }
        <div className={classes.title}>{title}</div>
    </Grid>
  );
};

export default Wallet;
