import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { Portfolio, TokenListInfo, TrendingCrypto, TrendingNews } from './components';
import { Grid } from '@material-ui/core';
import axios from 'axios';
import { useSelector } from 'react-redux';
import { TRENDING_LIST } from 'utils/constants';
import { getPrice } from 'utils/price';
import { BIT_CONFIG, WETH_ADDRESS, USDT_ADDRESS, DAI_ADDRESS } from "utils/constants";

const Dashboard = props => {
  const account = useSelector(state => state.app.currentWallet);
  const [walletData, setWalletData] = useState([]);
  const [trendingData, setTrendingData] = useState([]);

  const getDataQuery = `
    {
      ethereum(network: ethereum) {
        address(address: {is: "${account}"}){
          balances {
            value
            currency {
              address
              symbol
              tokenType
              decimals
            }
          }
        }
      }
    }`;

  const getCurrentDate = (separator = '') => {

    let newDate = new Date()
    let date = newDate.getDate();
    let month = newDate.getMonth() + 1;
    let year = newDate.getFullYear();

    return `${year}${separator}${month < 10 ? `0${month}` : `${month}`}${separator}${date}`
  }

  useEffect(() => {
    if (account === undefined)
      return;
    fetchData();
  }, [account])

  useEffect(() => {
    // fetchTrendingData();
    fetchTrendingBitqueryData();
  }, [])

  const fetchData = async () => {
    const queryResult = await axios.post(process.env.REACT_APP_BITQUERY_API, { query: getDataQuery }, BIT_CONFIG);
    let { balances } = queryResult.data.data.ethereum.address[0];
    balances = (balances || []).filter((balance) => balance.value !== 0);

    let prices = [];
    if (balances && balances.length > 0) {
      balances = (balances || []).filter((balance) => balance.value > 0)
      prices = await Promise.all(balances.map(async (elem) => {
        let { address } = elem.currency;
        if (address === '-') {
          address = WETH_ADDRESS;
        }
        return await getPrice(address);
      }))
    }

    balances = balances.map((balance, i) => {
      balance.price = prices[i];
      return balance;
    })
    balances = balances.filter((balance) => balance.price !== 0);

    const today = getCurrentDate('-');
    const data = await Promise.all(balances.map(async (elem) => {
      let quoter = USDT_ADDRESS;
      if (elem.currency.address === '-') {
        elem.currency.address = WETH_ADDRESS;
      }
      else if (elem.currency.address === USDT_ADDRESS) {
        quoter = DAI_ADDRESS;
      }

      let item = {};
      item.address = elem.currency.address;
      item.symbol = elem.currency.symbol;
      item.price = '$' + elem.price;
      item.balance = elem.value.toFixed(4) + ' ' + elem.currency.symbol;
      item.value = '$' + (elem.value * elem.price).toFixed(2);
      item.change = '0%';
      item.logo = '';

      const priceQuery = `
      {
          ethereum(network: ethereum) {
            dexTrades(
              options: {desc: "timeInterval.minute", limit: 1}
              date: {before: "${today}"}
              protocol: {is: "Uniswap v2"}
              baseCurrency: {is: "${elem.currency.address}"}
              quoteCurrency: {is: "${quoter}"}
            ) {
              exchange {
                name
              }
              timeInterval {
                minute(count: 1)
              }
              baseCurrency {
                symbol
                address
              }
              baseAmount
              quoteCurrency {
                symbol
                address
              }
              close_price: maximum(of: time, get: quote_price)
            }
          }
      }`
      const queryPriceResult = await axios.post(process.env.REACT_APP_BITQUERY_API, { query: priceQuery }, BIT_CONFIG)
      const { dexTrades } = queryPriceResult.data.data.ethereum;
      if (dexTrades !== null && dexTrades.length > 0) {
        const oldPrice = parseFloat(dexTrades[0].close_price);
        const change = ((oldPrice - elem.price) / oldPrice * 100).toFixed(2);
        if (change < 0) {
          item.change = '-' + Math.abs(change) + '%';
        }
        else {
          item.change = '+' + change + '%';
        }
      }

      return item;
    }));

    setWalletData(data);
  }

  const fetchTrendingData = async () => {
    const data = await Promise.all(TRENDING_LIST.map(async (cell) => {
      let item = {};
      item.symbol = cell.symbol;
      item.price = '$' + (await getPrice(cell.address)).toFixed(2);
      return item;
    }));

    setTrendingData(data);
  }

  const fetchTrendingBitqueryData = async () => {
    const data = await Promise.all(TRENDING_LIST.map(async (cell) => {
      const priceQuery = `
      {
        ethereum(network: ${cell.network}) {
                dexTrades(
                  options: {desc: "timeInterval.minute", limit: 1}
                  protocol: {is: "Uniswap v2"}
                  baseCurrency: {is: "${cell.address}"}
                  quoteCurrency: {is: "${cell.quoter}"}
                ) {
                  exchange {
                    name
                  }
                  timeInterval {
                    minute(count: 1)
                  }
                  baseCurrency {
                    symbol
                    address
                  }
                  baseAmount
                  quoteCurrency {
                    symbol
                    address
                  }
                  close_price: maximum(of: time, get: quote_price)
                }
              }
      }`
      const queryResult = await axios.post(process.env.REACT_APP_BITQUERY_API, { query: priceQuery }, BIT_CONFIG)
      const { dexTrades } = queryResult.data.data.ethereum
      let item = {};
      item.symbol = cell.symbol;
      if (dexTrades && dexTrades.length > 0)
        item.price = '$' + parseFloat(dexTrades[0].close_price).toFixed(2);
      else
        item.price = '$0';
      item.logo = cell.logo;
      return item;
    }));

    setTrendingData(data);
  }

  return (
    <>
      <TokenListInfo data={walletData} />
      <Grid container spacing={2}>
        <Grid item xs={12} sm={8}>
          <Portfolio walletData={walletData} />
        </Grid>
        <Grid item xs={12} sm={4}>
          <TrendingCrypto data={trendingData} />
        </Grid>
        {/* <Grid item xs={12}>
          <TrendingNews />
        </Grid> */}
      </Grid>
    </>
  );
};

export default Dashboard;
