import React, { useEffect, useRef, useState } from 'react';
import { SiteInfoContextConsumer } from 'App';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { AppBar, Box, Button, Card, Fade, Paper } from '@material-ui/core';
import useStyles from './style';
import { useHistory } from 'react-router-dom';
import { CheckIcon, ConnectWalletIcon, DarkIcon, EthLogoIcon, HamburgerIcon, LightIcon, ManageWalletIcon } from './svgs/icons';
import { setCurrentWallet, setWalletList } from 'store/actions/App';
import { useDispatch, useSelector } from 'react-redux';
import { getReducedWalletAddress } from 'utils/services';
import { Close } from '@material-ui/icons';

const WalletCard = props => {
  const {
    handleClose
  } = props;
  const dispatch = useDispatch();
  const history = useHistory();
  const walletList = useSelector(state => state.app.walletList);
  const currentWallet = useSelector(state => state.app.currentWallet);
  const classes = useStyles();

  return (
    <Box className={classes.root}>
      <Box style={{display: 'flex', justifyContent: 'space-between'}}>
        <Box>
          My Wallets
        </Box>
        <Close onClick={handleClose}/>
      </Box>
      {
        walletList.map((item, index) => (
          <Box className={classes.control} onClick={() => dispatch(setCurrentWallet(item.address))}>
            <Box className={classes.rightArea}>
              {getReducedWalletAddress(item.address)}
              {
                item.address.toLowerCase() == (currentWallet || '').toLowerCase() && <CheckIcon className={classes.check}/>
              }
            </Box>
          </Box>
        ))
      }
      <Box className={classes.control} onClick={() => history.push('/connect-wallet')}>
        <ConnectWalletIcon/>
        <Box className={classes.rightArea}>
          Connect another wallet
        </Box>
      </Box>
      <Box className={classes.control}>
        <ManageWalletIcon/>
        <Box className={classes.rightArea}>
          Manage wallets
        </Box>
      </Box>
    </Box>
  );
};

export default WalletCard;
