import React, { useState } from 'react';
import { Table, TableHead, TableRow, TableBody, TableCell, TableFooter } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { UpIcon, DownIcon } from './icon/icon';
//MuiGrid-root MuiGrid-container MuiGrid-item
const useStyles = makeStyles(theme => ({
  margin: {
    width: props => props.width,
    '& .MuiSelect-select.MuiSelect-select': {
      borderColor: '#1499ff'
    },
    '& .MuiNativeSelect-icon': {
      color: '#1499ff'
    },
  },
  table: {

  },
  root: {
    '& thead button': {
      background: 'transparent',
      outline: 'transparent',
      border: '0px',
      fontFamily: 'Lato',
      fontStyle: 'normal',
      fontWeight: '400',
      fontSize: '16px',
      lineHeight: '36px',
      color: theme.palette.text_main,
      width: '100%'
    },
    '& .MuiTableCell-root': {
      fontSize: 14,
      padding: 11
    },
    '& .MuiTableRow-root': {
      fontFamily: 'Lato',
      fontStyle: 'normal',
      background: theme.palette.topbar_background,
    },
    '& .MuiTableCell-body': {
      color: theme.palette.text_sub
    }
  },
  body: {
    marginBottom: 11,
    '& .MuiPaginationItem-textPrimary.Mui-selected': {
      background: 'linear-gradient(90deg, #00b8d4 10%, #00bf82 90%)',
    },
    '& .MuiPaginationItem-root': {
      fontSize: 15,
      width: 33,
      height: 33,
    }
  },
  hide: {
    visibility: 'hidden'
  },
  show: {
    visibility: 'visible'
  },
  symbol: {
    display: 'flex',
    alignItems: 'center',
    gap: '3px'
  },
  logo: {
    background: 'rgba(178, 183, 255, 0.2)',
    borderRadius: '8px',
    height: '40px',
    width: '40px',
    borderRadius: '8px',
    justifyContent: 'center',
    alignItems: 'center',
    display: 'flex',
  },
  change_minus: {
    color: '#FF8585'
  },
  change_plus: {
    color: '#3AC090'
  }
}));

export default function TableCard(props) {
  const classes = useStyles();
  const [direction, setDirection] = useState(props.columns);
  const tempDirection = props.columns;
  let tempDirect = [];
  if (tempDirection) {
    for (let i = 0; i < tempDirection.length; i++)
      tempDirect[i] = '/images/sort_down.png';
  }
  const [cells] = useState(props.cells);

  const items = props.products;
  const footer = props.footerItems ? props.footerItems : [];
  const [direct, setDirect] = React.useState(tempDirect);

  const Sort = (index = 0) => {
    if (direction[index] === "ASC") {
      tempDirection[index] = "DESC";
      tempDirect[index] = <UpIcon />;
      setDirect(tempDirect);
      setDirection(tempDirection);
    } else {
      tempDirection[index] = "ASC";
      tempDirect[index] = <DownIcon />;
      setDirect(tempDirect);
      setDirection(tempDirection);
    }

    props.onSelectSort(index, tempDirection[index]);
  }

  return (
    <Grid container direction="column" spacing={2} className={classes.table}>
      <Grid item container style={{ overflowX: 'auto' }}>
        <Table className={classes.root}>
          <TableHead>
            <TableRow >
              {
                cells.map((cell, i) => (
                  <TableCell key={i} style={{ width: 100 / props.columns.length + '%' }}>
                    <button
                      type="button"
                      onClick={() => Sort(i)}
                      style={{ display: 'flex', alignItems: 'center' }}
                    >
                      {cell.field}
                      {direct[i]}
                    </button>
                  </TableCell>
                ))
              }
            </TableRow>
          </TableHead>
          <TableBody>
            {items.map((item, i) => (
              <TableRow key={i}>
                {
                  cells.map((cell, j) => {
                    const value = item[cell.key];
                    if (cell.key === 'symbol' && item.logo !== '') {
                      return (
                        <TableCell
                          key={j}
                          style={{ width: 100 / (props.columns.length) + '%' }}
                        >
                          <div className={classes.symbol}>
                            <div className={classes.logo}>
                              <img src={item.logo} width="30" height="30" />
                            </div>
                            {
                              value
                            }
                          </div>
                        </TableCell>
                      );
                    }
                    else if (cell.key === 'change') {
                      return (
                        <TableCell
                          key={j}
                          style={{ width: 100 / (props.columns.length) + '%' }}
                        >
                          <div className={value.startsWith('+') ? classes.change_plus : value.startsWith('-') ? classes.change_minus : classes.change_normal}>
                            {
                              value
                            }
                          </div>
                        </TableCell>
                      );
                    }
                    else {
                      return (
                        <TableCell
                          key={j}
                          style={{ width: 100 / (props.columns.length) + '%' }}
                        >
                          {
                            value
                          }
                        </TableCell>
                      );
                    }
                  })
                }
              </TableRow>
            ))}
          </TableBody>
          <TableFooter className={(props.tblFooter === 'true' || items.length === 0) ? classes.show : classes.hide}>
            {
              items.length === 0 ?
                <TableRow>
                  <TableCell colSpan="100%" style={{ textAlign: 'center' }}>No data found</TableCell>
                </TableRow>
                :
                <TableRow>
                  {
                    footer.map((footerItem, i) => {
                      return (
                        <TableCell key={i}>
                          {footerItem}
                        </TableCell>
                      );
                    })
                  }
                </TableRow>
            }
          </TableFooter>
        </Table>
      </Grid>
    </Grid>
  );
};
