import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/styles';
import useStyles from './style';
import { ExpandCard, TableCard } from 'components';
import { Popover, Box, Card } from '@material-ui/core';
import { InfoIcon } from 'assets/svgs/icon';


const TokenListInfo = props => {
  const { children, data } = props;
  const theme = useTheme();
  const classes = useStyles(theme);
  const [dataList, setDataList] = useState([]);
  const [allDataList, setAllDataList] = useState([]);
  const [sort_column, setSortColumn] = useState(0);
  const [sort_method, setSortMethod] = useState("ASC");
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const columns = [];
  const cellList = [
    { key: 'symbol', field: 'Your Crypto' },
    { key: 'price', field: 'Market price' },
    { key: 'balance', field: 'Balance' },
    { key: 'value', field: 'Value($)' },
    { key: 'change', field: 'Change' },
  ];

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleSort = (index, direct) => {
    setSortColumn(index);
    setSortMethod(direct);
  }

  const replaceString = (str) => {
    if (!str)
      return str;
    let val = str.split(' ')[0];
    val = val.replace('$', '').replace('%', '');
    console.log(val)
    return val;
  }

  function updateTable() {
    var listData = [...allDataList];
    var sortmethod = sort_method === "ASC" ? 1 : -1;
    var startIndex = 0;
    var endIndex = listData.length;

    listData.sort(function (a, b) {

      var nameA = a[`${cellList[sort_column].key}`] === null || a[`${cellList[sort_column].key}`] === undefined ? "" : a[`${cellList[sort_column].key}`].toLowerCase();
      var nameB = b[`${cellList[sort_column].key}`] === null || b[`${cellList[sort_column].key}`] === undefined ? "" : b[`${cellList[sort_column].key}`].toLowerCase();
      nameA = replaceString(nameA);
      nameB = replaceString(nameB);
      if (isNaN(nameA)) {
        if (nameA < nameB)
          return -1 * sortmethod;
        if (nameA > nameB)
          return 1 * sortmethod;
        return 0; 
      }
      else {
        if (Number(nameA) < Number(nameB)) 
          return -1 * sortmethod;
        if (Number(nameA) > Number(nameB))
          return 1 * sortmethod;
        return 0; 
      }
    });

    setAllDataList(listData);
    setDataList(listData.slice(startIndex, endIndex));
  }

  useEffect(() => {
    updateTable();
  }, [sort_column, sort_method]);

  useEffect(() => {
    if (data === undefined)
      return;

    setAllDataList(data);
    setDataList(data);

  }, [data]);

  return (
    <div className={classes.root}>
      <Box className={classes.top_bar}>
        <Box className={classes.header}>Your Crypto Wallet</Box>
        <Box className={classes.info} onClick={handleClick}>
          More info
          <InfoIcon className={classes.icon} />
        </Box>
      </Box>
      <ExpandCard>
        <TableCard
          onSelectSort={handleSort}
          columns={columns}
          products={dataList}
          cells={cellList}
        />
      </ExpandCard>
      <Popover
        className={classes.popover}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}>
        <Card className={classes.card}>
          <Box className={classes.main}>
            Market Price
          </Box>
          <Box className={classes.sub}>
            The market price is the current price at which a crypto can be bought or sold.
          </Box>
          <Box className={classes.divide}/>
          <Box className={classes.main}>
            Balance
          </Box>
          <Box className={classes.sub}>
            The balance reflects your current crypto holdings.
          </Box>
          <Box className={classes.divide}/>
          <Box className={classes.main}>
            Value ($)
          </Box>
          <Box className={classes.sub}>
            The value in $ represents the total value of your crypto represented in Fiat. It is calculated as follows: Balance of your holdings x Market Price.
          </Box>
          <Box className={classes.divide}/>
          <Box className={classes.main}>
            Change
          </Box>
          <Box className={classes.sub}>
          The percentage increase or decrease in a crypto asset.
          </Box>
        </Card>
      </Popover>
    </div>
  );
};

export default TokenListInfo;
