import React, { useEffect, useRef, useState } from 'react';
import { SiteInfoContextConsumer } from 'App';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { AppBar, Box, Button, Card, Fade, Paper, Popover } from '@material-ui/core';
import useStyles from './style';
import { useHistory } from 'react-router-dom';
import { DarkIcon, EthLogoIcon, HamburgerIcon, LightIcon } from './svgs/icons';
import SearchInput from 'components/SearchInput';
import SearchImg from 'components/SearchImg';
import { setCurrentWallet, setWalletList } from 'store/actions/App';
import { useDispatch, useSelector } from 'react-redux';
import { getReducedWalletAddress } from 'utils/services';
import { KeyboardArrowDown, KeyboardArrowUp } from '@material-ui/icons';
import Popper from '@material-ui/core/Popper';
import Typography from 'theme/typography';
import WalletCard from '../WalletCard';

const Topbar = props => {
  const {
    className,
    onSidebarOpen,
    onSidebarClose,
    openSidebar,
    ...rest
  } = props;
  const dispatch = useDispatch();
  const walletList = useSelector(state => state.app.walletList);
  const currentWallet = useSelector(state => state.app.currentWallet);

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const classes = useStyles();
  let history = useHistory();
  const onMaxTopbar = () => {
    if (openSidebar === false) onSidebarOpen();
    else onSidebarClose();
  };

  useEffect(() => {
    let _walletList = JSON.parse(localStorage.getItem('wallets') || '[]');
    dispatch(setWalletList(_walletList));
    if (_walletList.length != 0) {
      dispatch(setCurrentWallet(_walletList[0].address));
    }
  }, []);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <SiteInfoContextConsumer>
      {props => (
        <AppBar
          {...rest}
          className={clsx(classes.root, className)}>
          <div className={classes.toolbar}>
            <div className={classes.leftContainer}>
              <div className={classes.titlebar}>
                <div
                  className={classes.close_drawer_icon}
                  onClick={onMaxTopbar}>
                  <HamburgerIcon />
                </div>
              </div>
              <div className={classes.searchbar}>
                <SearchInput placeholder="Search" />
              </div>
            </div>
            <div className={classes.rightControllerArea}>
              <div className={classes.searchIcon}>
                <SearchImg />
              </div>
              <div
                className={classes.helper}
                onClick={e => {
                  e.preventDefault();
                  props.toggleContrast();
                }}>
                {props.is_contrast ? <DarkIcon /> : <LightIcon />}
              </div>
              <div className={classes.avatar}>
                {walletList.length == 0 ? (
                  <Button
                    variant="contained"
                    className={classes.connect_button}
                    onClick={() => {
                      history.push('/connect-wallet');
                    }}>
                    Connect Wallet
                  </Button>
                ) : (
                  <Button
                    variant="contained"
                    className={classes.connect_button}
                    onClick={handleClick}>
                    <EthLogoIcon className={classes.eth_icon} />
                    <Box>{getReducedWalletAddress(currentWallet)}</Box>
                    {!open ? <KeyboardArrowDown /> : <KeyboardArrowUp />}
                  </Button>
                )}
              </div>
            </div>
          </div>
          <Popover
            className={classes.popover}
            open={open}
            anchorEl={anchorEl}
            onClose={handleClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}>
              <Card className={classes.card}>
                <WalletCard handleClose={handleClose} />
              </Card>
          </Popover>
        </AppBar>
      )}
    </SiteInfoContextConsumer>
  );
};

Topbar.propTypes = {
  className: PropTypes.string,
  onSidebarOpen: PropTypes.func
};

export default Topbar;
