import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { useSelector } from 'react-redux';
import axios from 'axios';
import { BIT_CONFIG, WETH_ADDRESS, USDT_ADDRESS, DAI_ADDRESS, USDC_ADDRESS, BUSD_ADDRESS } from "utils/constants";
import useStyles from './style';
import { NormalCard } from 'components';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer
} from 'recharts';
import { Grid, List, ListItem, Box, Popover, Card } from '@material-ui/core';
import { DownIcon, UpIcon } from './icon/icon';
import { InfoIcon } from 'assets/svgs/icon';

const CHART_LINE_NUM = 60;
const Portfolio = props => {
  const { walletData } = props;
  const account = useSelector(state => state.app.currentWallet);
  const theme = useTheme();
  const classes = useStyles(theme);
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [data, setData] = useState([]);
  const [startTime, setStartTime] = useState(new Date().toISOString());
  const [intervalVal, setIntervalVal] = useState(1);
  const [currentPrice, setCurrentPrice] = useState(0);
  const [changePrice, setChangePrice] = useState(0);
  const [change, setChange] = useState(0);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const IntervalArray = [1, 12, 84, 360, 4320, 8640];

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  useEffect(() => {
    const now = new Date().getTime();
    let before = 0;
    switch (selectedIndex) {
      case 0:
        before = now - 60 * 60 * 1000;
        break;
      case 1:
        before = now - 24 * 60 * 60 * 1000;
        break;
      case 2:
        before = now - 7 * 24 * 60 * 60 * 1000;
        break;
      case 3:
        before = now - 30 * 24 * 60 * 60 * 1000;
        break;
      case 4:
        before = now - 12 * 30 * 24 * 60 * 60 * 1000;
        break;
      case 5:
        before = now - 2 * 12 * 30 * 24 * 60 * 60 * 1000;
        break;
    }

    let _interval = (now - before) / CHART_LINE_NUM;
    setIntervalVal(_interval);

    setStartTime(new Date(before).toISOString());
  }, [selectedIndex]);

  useEffect(() => {
    if (walletData && walletData.length > 0 && account) {
      fetchChartData();
    }
  }, [walletData, startTime, account])

  const getTimeLine = () => {
    let ret = [];
    const now = new Date().toISOString();
    const d = (new Date(now).getTime() - new Date(startTime).getTime()) / CHART_LINE_NUM;
    for (let i = 0; i < CHART_LINE_NUM; i++) {
      ret.push(new Date(new Date(startTime).getTime() + (CHART_LINE_NUM - i) * d).toISOString())
    }

    return ret;
  }


  const getPosition = (time, start, interval) => {
    return Math.floor((time - start) / interval);
  }

  const fetchChartData = async () => {
    const endTime = new Date().toISOString();
    const timeline = getTimeLine();
    let chartDataArray = await Promise.all(walletData.map(async (cell) => {
      const getChartDataQuery = `
        {
            ethereum(network: ethereum) {
              dexTrades(
                options: {desc: "timeInterval.minute", limit: ${CHART_LINE_NUM}}
                date: {since: "${startTime}", till: "${endTime}"}
                protocol: {is: "Uniswap v2"}
                baseCurrency: {is: "${cell.address}"}
                quoteCurrency: {in: ["${USDT_ADDRESS}", "${DAI_ADDRESS}", "${USDC_ADDRESS}", "${BUSD_ADDRESS}"]}
              ) {
                exchange {
                  name
                }
                timeInterval {
                  minute(count: ${IntervalArray[selectedIndex]})
                }
                baseCurrency {
                  symbol
                  address
                }
                baseAmount
                quoteCurrency {
                  symbol
                  address
                }
                close_price: maximum(of: time, get: quote_price)
              }
            }
        }`
      const queryResult = await axios.post(process.env.REACT_APP_BITQUERY_API, { query: getChartDataQuery }, BIT_CONFIG)
      const { dexTrades } = queryResult.data.data.ethereum
      const valueQuery =
        `{
          ethereum {
            address(address: {is: "${account}"}) {
              balances(currency: {is: "${cell.address === WETH_ADDRESS ? "ETH" : cell.address}"}) {
                history {
                  value
                  timestamp
                }
                value
                currency{
                  address
                }
              }
            }
          }
        }`;

      const valueResult = await axios.post(process.env.REACT_APP_BITQUERY_API, { query: valueQuery }, BIT_CONFIG)
      const history_val = valueResult.data.data.ethereum.address[0]?.balances && valueResult.data.data.ethereum.address[0]?.balances[0];

      if (history_val === null)
        return [];

      let history = history_val.history;

      if (history === null)
        return [];
      let historyIndex = history.length - 1;
      if (dexTrades.length === 0)
        return [{
          name: startTime.replace('T', ' ').replace('Z', '').split(".")[0],
          price: Number(cell.price.replace("$", "")),
          value: history[historyIndex].value
        }];


      const data = dexTrades.map((cell) => {
        const dexDate = new Date(cell.timeInterval.minute).getTime();
        const historyDate = new Date(history[historyIndex].timestamp).getTime();
        let item = {};

        if (historyDate < dexDate) {
          item.price = parseFloat(cell.close_price) * parseFloat(history[historyIndex].value);
          item.value = history[historyIndex].value;
        }
        else {
          let findHistoryData = historyDate;
          while (historyIndex > 0 && (findHistoryData > dexDate)) {
            historyIndex = historyIndex - 1;
            findHistoryData = new Date(history[historyIndex].timestamp).getTime();
          }

          if (historyIndex === 0 && findHistoryData > dexDate) {
            item.price = 0;
            item.value = 0;
          }
          else {
            item.price = parseFloat(cell.close_price) * parseFloat(history[historyIndex].value);
            item.value = history[historyIndex].value;
          }
        }

        item.name = cell.timeInterval.minute;
        return item;
      })

      return data
    }));

    chartDataArray = chartDataArray.filter(cell => cell.length > 0);
    let chartData = [];
    for (let i = 0; i < chartDataArray.length; i++) {
      if (chartDataArray[i].length > 0) {
        if (chartDataArray[i].length > 1) {
          for (let j = chartDataArray[i].length - 1; j > 0; j --) {
            let item1 = chartDataArray[i][j];
            let item2 = chartDataArray[i][j - 1];
            let position1 = getPosition(new Date(item1.name).getTime(), new Date(startTime.replace('T', ' ').replace('Z', '').split(".")[0]).getTime(), intervalVal);
            let position2 = getPosition(new Date(item2.name).getTime(), new Date(startTime.replace('T', ' ').replace('Z', '').split(".")[0]).getTime(), intervalVal);
            if (position1 < 0)
              position1 = 0;
            if (position2 < 0)
              continue;
            if(position1 == position2)
              continue;
            for (let k = position1; k < position2; k ++) {
              chartData[k] = {
                name: timeline[k],
                price: (chartData[k]?.price || 0) + item1.price
              }
            }
          }
          let item1 = chartDataArray[i][0];
          let position1 = getPosition(new Date(item1.name).getTime(), new Date(startTime.replace('T', ' ').replace('Z', '').split(".")[0]).getTime(), intervalVal);
          for (let k = position1; k < CHART_LINE_NUM; k ++) {
            chartData[k] = {
              name: timeline[k],
              price: (chartData[k]?.price || 0) + item1.price
            }
          }
        } else {
          let item1 = chartDataArray[i][0];
          let position1 = getPosition(new Date(item1.name).getTime(), new Date(startTime.replace('T', ' ').replace('Z', '')).getTime(), intervalVal);
          if (position1 < 0 || position1 > CHART_LINE_NUM)
            position1 = 0;
          for (let k = position1; k < CHART_LINE_NUM; k ++) {
            chartData[k] = {
              name: timeline[k],
              price: (chartData[k]?.price || 0) + item1.price
            }
          }
        }
      }
    }

    setData(chartData);
  }

  useEffect(() => {
    if (data.length > 0) {
      setChangePrice((data[data.length - 1].price - data[data.length - 2].price).toFixed(2));
      setChange((data[data.length - 1].price / data[data.length - 2].price - 1).toFixed(3));
      setCurrentPrice((data[data.length - 1].price).toFixed(2))
    }
  }, [data])

  return (
    <div className={classes.root}>
      <div className={classes.header}>Your Total Portfolio</div>
      <NormalCard>
        <Box className={classes.top_bar}>
          <Box className={classes.current_price}>${currentPrice}</Box>
          <Box className={classes.info} onClick={handleClick}>
            More info
            <InfoIcon className={classes.icon} />
          </Box>
        </Box>
        <Box
          className={
            changePrice > 0
              ? classes.change_plus
              : changePrice < 0
              ? classes.change_minus
              : classes.change_normal
          }>
          <Box className={classes.price}>
            {changePrice > 0 ? `+${changePrice}` : changePrice}
          </Box>
          <Box style={{display: 'flex', alignItems: 'center'}}>
            {changePrice > 0 ? (
              <UpIcon />
            ) : changePrice < 0 ? (
              <DownIcon />
            ) : (
              <></>
            )}
            <Box>{Math.abs(change)}%</Box>
          </Box>
        </Box>
        <div style={{ height: 300, marginLeft: -8 }}>
          <ResponsiveContainer>
            <AreaChart
              width={500}
              height={400}
              data={data}
              margin={{
                top: 10,
                right: 30,
                left: 0,
                bottom: 0
              }}>
              <defs>
                <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="0%" stopColor="#8087ED" stopOpacity={1} />
                  <stop offset="100%" stopColor="#8087ED" stopOpacity={0} />
                </linearGradient>
              </defs>
              <CartesianGrid vertical={false} />
              <XAxis dataKey="none" axisLine={false} tickLine={false} />
              <YAxis axisLine={false} tickLine={false} />
              <Tooltip />
              <Area
                type="monotone"
                dataKey="price"
                stroke="#B2B7FF"
                fill="url(#colorUv)"
                activeDot={{ stroke: '#8087ED', strokeWidth: 1, r: 6 }}
              />
            </AreaChart>
          </ResponsiveContainer>
        </div>
        <List style={{ display: 'flex' }}>
          <ListItem
            key="1D"
            selected={selectedIndex === 0}
            className={classes.button}
            onClick={() => setSelectedIndex(0)}>
            1H
          </ListItem>
          <ListItem
            key="1H"
            selected={selectedIndex === 1}
            className={classes.button}
            onClick={() => setSelectedIndex(1)}>
            1D
          </ListItem>
          <ListItem
            key="1W"
            selected={selectedIndex === 2}
            className={classes.button}
            onClick={() => setSelectedIndex(2)}>
            1W
          </ListItem>
          <ListItem
            key="1M"
            selected={selectedIndex === 3}
            className={classes.button}
            onClick={() => setSelectedIndex(3)}>
            1M
          </ListItem>
          <ListItem
            key="1Y"
            selected={selectedIndex === 4}
            className={classes.button}
            onClick={() => setSelectedIndex(4)}>
            1Y
          </ListItem>
          <ListItem
            key="ALL"
            selected={selectedIndex === 5}
            className={classes.button}
            onClick={() => setSelectedIndex(5)}>
            ALL
          </ListItem>
        </List>
      </NormalCard>
      <Popover
        className={classes.popover}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}>
        <Card className={classes.card}>
          <Box className={classes.main}>
            Your Total Portfolio
          </Box>
          <Box className={classes.sub}>
            Your total portfolio is a Fiat $ value representation of your total crypto holdings over time.
          </Box>
        </Card>
      </Popover>
    </div>
  );
};

export default Portfolio;
