/* eslint-disable react/no-multi-comp */
/* eslint-disable react/display-name */
import React, { forwardRef, useState } from 'react';
import { NavLink as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import {
  List,
  ListItem,
  Button,
  Tooltip
} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  root: {},
  item: {
    display: 'flex',
    paddingTop: 0,
    paddingBottom: 0
  },
  button: {
    padding: '18px 16px 18px 42px',
    justifyContent: 'flex-start',
    textTransform: 'none',
    letterSpacing: 0,
    width: '100%',
    fontWeight: 600,

    color: theme.palette.sidebar_color,
    lineHeight: '1em',
    '&:hover': {
      backgroundColor: theme.palette.sidebar_active_background,
      color: theme.palette.sidebar_active_color,
      fontWeight: 600,
      borderRadius: '0px',
    }
  },
  icon: {
    width: 20,
    height: 20,
    display: 'flex',
    alignItems: 'center',
    marginRight: theme.spacing(3)
  },
  active: {
    fontWeight: 600,
    '& path': {
      stroke: theme.palette.sidebar_hover_color
    },
    '& $icon': {
      color: theme.palette.sidebar_active_color
    },
    backgroundColor: theme.palette.sidebar_active_background,
    color: theme.palette.sidebar_active_font_color,
    borderRadius: '0px',
    borderRight: `4px solid ${theme.palette.sidebar_active_border}` 
  },
  title: {
    width: '150px'
  },
}));

const CustomRouterLink = forwardRef((props, ref) => (
  <div ref={ref} style={{ flexGrow: 1 }}>
    <RouterLink {...props} />
  </div>
));

const SidebarNav = props => {
  const { pages, className, history, ...rest } = props;
  const [open, setOpen] = useState(false);
  const classes = useStyles();
  const handleClick = page => {
    if (page.sub) {
      setOpen(!open);
    } else {
      history.push(page.href);
    }
  };
  return (
    <List {...rest} className={clsx(classes.root, className)}>
      {pages.map(page => (
        <>
          <ListItem
            className={classes.item}
            disableGutters
            key={page.title}
            onClick={() => handleClick(page)}>
            {page.tooltip ? (
              <Tooltip arrow title={page.tooltip} placement="right-start">
                <Button
                  activeClassName={
                    classes.active
                  }
                  className={classes.button}
                  component={CustomRouterLink}
                  to={page.href}>
                  <div className={classes.icon}>{page.icon}</div>
                  <div className={classes.title}>{page.title}</div>
                </Button>
              </Tooltip>
            ) : (
              <Button
                activeClassName={
                  classes.active
                }
                className={classes.button}
                component={CustomRouterLink}
                to={page.href}>
                <div className={classes.icon}>{page.icon}</div>
                <div className={classes.title}>{page.title}</div>
              </Button>
            )}
          </ListItem>
        </>
      ))}
    </List>
  );
};

SidebarNav.propTypes = {
  className: PropTypes.string,
  pages: PropTypes.array.isRequired
};

export default SidebarNav;
