import React from 'react';
import { Switch, Redirect } from 'react-router-dom';

import { RouteWithLayout } from './components';
import { Main as MainLayout, Normal as NormalLayout } from './layouts';

import {
  Dastboard as DashboardView,
  Swap as SwapView,
  Send as SendView,
  Wallet as WalletView,
  Address as AddressView,
  Nfts as NftsView,
  Education as EducationView,
  Settings as SettingsView,
  ConnectWallet as ConnectWalletView,
  ConnectMagic as ConnectMagicView,
  NotFound as NotFoundView,
} from './views';

const Routes = () => {
  return (
    <Switch>
      <Redirect exact from="/" to="/dashboard"/>
      <RouteWithLayout
        component={DashboardView}
        exact
        layout={MainLayout}
        title='Good Afternoon'
        description='Welcome to Clutch Connect, a powerful, secure and simple DeFi platform.'
        path="/dashboard"
      />
      <RouteWithLayout
        component={SwapView}
        exact
        layout={MainLayout}
        title='Swap'
        description='Easily swap one crypto for another'
        path="/swap"
      />
      <RouteWithLayout
        component={SendView}
        exact
        layout={MainLayout}
        title='Send Crypto'
        description="Send crypto to another wallet"
        path="/send"
      />
      <RouteWithLayout
        component={WalletView}
        exact
        layout={MainLayout}
        title='Wallets'
        path="/wallets"
      />
      <RouteWithLayout
        component={AddressView}
        exact
        layout={MainLayout}
        title='Address Book'
        path="/address"
      />
      <RouteWithLayout
        component={NftsView}
        exact
        layout={MainLayout}
        title="NFT's"
        path="/nfts"
      />
      <RouteWithLayout
        component={EducationView}
        exact
        layout={MainLayout}
        title="Education"
        path="/education"
      />
      <RouteWithLayout
        component={SettingsView}
        exact
        layout={MainLayout}
        title="Settings"
        path="/settings"
      />
      <RouteWithLayout
        component={ConnectWalletView}
        exact
        layout={MainLayout}
        title="Connect to Clutch"
        path="/connect-wallet"
      />
      <RouteWithLayout
        component={ConnectMagicView}
        exact
        layout={MainLayout}
        title="Connect with email"
        path="/connect-wallet/magic"
      />
      <RouteWithLayout
        component={NotFoundView}
        exact
        layout={NormalLayout}
        path="/not-found"
      />
      <Redirect to="/not-found" />
    </Switch>
  );
};

export default Routes;
